@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.App {
  width: 860px;
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background: #3d93c5;
  color: #fff;
}
.card-grid {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.congrats {
  color: rgba(255, 68, 0, 0.747);
}

.soc-link {
  font-size: 1.5rem;
}

a {
  color: white;
}

a:hover {
  color: #3d93c5;
}

@media screen and (max-width: 900px) {
  .card img {
    width: 70px;
  }

  
}


.card {
  position: relative;
}
.card img {
  width: 10rem;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

/* front of card - the picture */
.card .front {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {

  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition-delay: 0s;
}


